import React from 'react';
import {Link} from "react-router-dom";

// Style sheets
import Style from './LocationButton.module.scss';
import '../../icons/weather-icons.css';
import '../../Default.scss';

export function LocationButton(props){
    const data = props.data;

    // Function to calculate which color to display depending on the tempreture of the location
    function calcColor(temp, tempType){
        let tempColor = null;

        if(tempType === "metric"){
            tempColor = (temp >= 36) 
                ? "Hot" : (temp >= 30) 
                    ? "Warmest" : (temp >= 25) 
                        ? "Warmer" : (temp >= 20) 
                            ? "Warm" : (temp >= 15) 
                                ? "Cold" : (temp >= 10) 
                                    ? "Colder" : (temp >= 0) 
                                        ? "Coldest" : "IceCold"
        } else if(tempType === "imperial"){  
            tempColor = (temp >= 96.8) 
                ? "Hot" : (temp >= 86) 
                    ? "Warmest" : (temp >= 77) 
                        ? "Warmer" : (temp >= 68) 
                            ? "Warm" : (temp >= 59) 
                                ? "Cold" : (temp >= 50) 
                                    ? "Colder" : (temp >= 32) 
                                        ? "Coldest" : "IceCold"
        }

        return `${Style.LocationButton} ${tempColor} animated fadeInDown`;
    }

    // Function to calculate the local time of the location
    function calcTime(timezone){
        const date = new Date();
        const time = date.setSeconds(timezone)
        const locationDate = new Date(time);

        const dateObject = {
            hour: locationDate.getUTCHours(),
            minute: locationDate.getUTCMinutes(),
        } 

        if(dateObject.minute >= 0 && dateObject.minute < 10){
            dateObject.minute = `0${dateObject.minute}`
        }

        if(dateObject.hour >= 0 && dateObject.hour < 10){
            dateObject.hour = `0${dateObject.hour}`
        }
        
        return `${dateObject.hour}:${dateObject.minute}`
    }

    // Function to calculate which type of icon to display if the night time icons or day time icons 
    function calcIcon(icon){
        const iconObject = icon;
        const iconArray = iconObject.icon.split('');
        const iconType = iconArray[iconArray.length - 1];

        if(iconType === "d"){
            return `wi wi-owm-day-${iconObject.id}`
        } else if(iconType === "n"){
            return `wi wi-owm-night-${iconObject.id}`
        } else{
            return `wi wi-owm-${iconObject.id}`
        }
    }

    return(
        <Link aria-label={`Enter to view ${data.name} weather details`} to="/location" className={Style.Link}>
            <button 
                aria-label={`${data.name} details`}
                className={calcColor(data.main.temp, props.tempType)}
                onClick={() => props.onClick(data)}>       
                <div className={Style.Left}>
                    <span aria-label="Weather icon" className={Style.Icon}><i className={calcIcon(data.weather[0])}></i></span>
                    <div className={Style.TitleContainer}>
                        <h6 className={Style.Date}>{calcTime(data.timezone)}</h6>
                        <h1 className={Style.Title}>{data.name}</h1>
                    </div>
                </div>
                <h6 className={Style.Temp}>{Math.round(data.main.temp)}</h6>
            </button>
        </Link>
    );
}