import React from 'react';

// Style sheets
import Style from './Figure.module.scss';
import '../../../../icons/weather-icons.css';

export function Figure(props){
    const location = props.location;

    // Function to calculate which type of icon to display if the night time icons or day time icons 
    function calcIcon(icon){
        const iconObject = icon;
        const iconArray = iconObject.icon.split('');
        const iconType = iconArray[iconArray.length - 1];

        if(iconType === "d"){
            return `wi wi-owm-day-${iconObject.id}`
        } else if(iconType === "n"){
            return `wi wi-owm-night-${iconObject.id}`
        } else{
            return `wi wi-owm-${iconObject.id}`
        }
    }

    return(
        <div className={Style.Figure}>
            <span aria-label="Weather icon" className={Style.Icon}><i className={location.weather[0].id === null ? "fas fa-bug" : calcIcon(location.weather[0])}></i></span>  
            <h6 className={Style.IconCaption}>{location.weather[0].description}</h6>
        </div>
    );
}